.downloadDocs {
  &.wrapper--narrow {
    @extend .wrapper;
  }

  ul {
    list-style: none;
    margin-left: 0;
    -webkit-text-size-adjust: none; // Webkit (safari ios) fix KBC-801
  }
  .accordion__link {
    background-color: $white;
    border: 1px solid $mediumGrey;
    margin-bottom: rem-calc(25*2/3);
    position: relative;
  }

  .btn-wrapper {
    padding: rem-calc(20*2/3) 0;
    > * {
      margin-bottom: 0;
    }
  }

  &__heading {
    display: table;
    padding-right: rem-calc(45);
    width: 100%;
  }
  &__list {
    margin-top: rem-calc(25*2/3);
  }
  &__title {
    display: table-cell;
    font-size: rem-calc(30*2/3);
    max-width: 70%;
    vertical-align: middle;
    &--sub {
      display: table-cell;
      font-size: rem-calc(20*2/3);
      text-align: right;
      vertical-align: middle;
    }
  }

  &__item {
    background-color: $lightGrey;
    margin-bottom: rem-calc(15*2/3);
    padding: rem-calc(20*2/3);
    display: table;
    width: 100%;

    > * {
      display: table-cell;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &__heading,
    &__date {
      font-size: rem-calc(20*2/3);
    }

    &__date {
      border-right: 1px solid $grey;
      padding-right: rem-calc(20*2/3);
    }

    &__info {
      padding-left: rem-calc(20*2/3);
    }

    &__heading {
      font-family: $font-regular;
      color: $darkGrey;
      margin-bottom: rem-calc(15*2/3);
    }

    &__link {
      .icon {
        font-size: rem-calc(30);
        color: $blue;
      }
    }
  }
}

@media #{$medium-up} {
  .downloadDocs {
    .accordion__link {
      padding: rem-calc(25);
      .display-status {
        top: rem-calc(30);
        right: rem-calc(25);
      }
    }

    .btn-wrapper {
      padding-top: rem-calc(20);
    }

    &__heading {
      padding: 0 rem-calc(70) 0 rem-calc(15);
    }
    &__title {
      font-size: rem-calc(30);
      &--sub {
        font-size: rem-calc(20);
      }
    }

    &__item {
      padding: rem-calc(15) rem-calc(25);

      &__heading,
      &__date {
        font-size: rem-calc(16);
      }

      &__heading {
        @include flex(1 1 auto);
        margin-bottom: 0;
      }

      &__info {
        @include display(flex);
        @include justify-content(space-between);
        @include align-items(center);
        padding-top: rem-calc(10);
        padding-bottom: rem-calc(10);
      }

      &__date {
        padding-top: rem-calc(10);
        padding-bottom: rem-calc(10);
      }

      &__link {
        > *:hover {
          span {
            color: $blue;
          }
        }
      }
    }
  }
}
